@import url(https://fonts.googleapis.com/css2?family=Orbitron&display=swap);
header {
  display: flex;
  border-bottom: solid black 1px;
  justify-content: center;
  align-items: center;
  height: 15vh;
}

form {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 20px;
  margin-top: 10px;
  margin-bottom: 10px;
}

form input {
  font-size: 1em;
  padding: 5px;
  margin: 10px;
}

form button {
  outline: none;
  background: none;
  padding: 10px;
  font-size: 0.8em;
  height: 100%;
}

form button:hover {
  color: white;
  background-color: black;
}

@media (max-width: 768px) {
  form {
    flex-direction: column;
  }
}

.image_container {
  display: flex;
  justify-content: center;
  position: relative;
  text-align: center;
  color: white;
  font-size: 1.7em;
  text-shadow: 3px 3px 0 #000, -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000;
}

.image_container img {
  width: 568px;
  height: 335px;
}

.image_container .topCenter {
  position: absolute;
  top: 15%;
  left: 50%;
  text-transform: uppercase;
  transform: translate(-50%, -50%);
}

.image_container .bottomCenter {
  position: absolute;
  top: 85%;
  left: 50%;
  text-transform: uppercase;
  transform: translate(-50%, -50%);
}

body {
  font-family: 'Orbitron', sans-serif;
  background-color: #ffff;
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
}

