form {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 20px;
  margin-top: 10px;
  margin-bottom: 10px;
}

form input {
  font-size: 1em;
  padding: 5px;
  margin: 10px;
}

form button {
  outline: none;
  background: none;
  padding: 10px;
  font-size: 0.8em;
  height: 100%;
}

form button:hover {
  color: white;
  background-color: black;
}

@media (max-width: 768px) {
  form {
    flex-direction: column;
  }
}
